import debug from "./log";
import constant from "./constant";
import axios from "axios";

const { BASE_URL, RESPONSE_OK } = constant;
debug.log(BASE_URL);
const Ajax = ({ method, url, data }) =>
  new Promise(async (resolve, reject) => {
    url = BASE_URL + url;
    const token = "NONE-TOKEN";
    const headers = {
      Authorization: token
    };
    axios({
      method,
      url,
      data,
      timeout: 2000,
      headers
    })
      .then(res => {
        debug.log(res);
        if (res.status === 200 && res.data && res.data.code === RESPONSE_OK) {
          resolve(res.data);
        } else if (
          res.status === 200 &&
          res.data &&
          res.data.code !== RESPONSE_OK
        ) {
          const err = {
            errCode: res.data.code || -1,
            errMsg: res.data.msg || "响应失败"
          };
          reject(err);
        } else {
          const err = {
            errCode: -1,
            errMsg: "请求失败"
          };
          reject(err);
        }
      })
      .catch(error => {
        const err = {
          errCode: -1,
          errMsg: "请求失败"
        };
        reject(err);
      });
  });

const get = (url, data) => {
  url =
    url +
    "?" +
    Object.keys(data)
      .map(k => encodeURIComponent(k) + "=" + encodeURIComponent(data[k]))
      .join("&");
  return Ajax({
    url,
    method: "get"
  });
};

const post = (url, data) =>
  Ajax({
    url,
    data,
    method: "post"
  });

export default {
  get,
  post
};
